/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Courier New', sans-serif;
  background-color: #1e1e2f;
  color: #e2e2e2;
  line-height: 1.6;
  font-size: 1.1rem;
  font-weight: 600;
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: center;
  background-color: #333547;
  padding: 10px;
  margin-bottom: 30px;
}

.navbar li {
  margin: 0 20px;
  list-style: none;
}

.navbar li a {
  color: #fff;
  text-decoration: none;
  font-size: 1.2rem;
}

.navbar li a:hover {
  color: #4a90e2;
}

/* Home Section */
.home {
  padding: 40px 20px;
  background-color: #2c2f3b;
  border-radius: 10px;
  margin-bottom: 30px;
  text-align: center;
}

.profile-photo {
  width: 250px;
  height: 250px;
  border-radius: 10%;
  margin-bottom: 20px;
  text-align: center;
}

h1 {
  font-size: 2.5rem;
  color: #fff;
}

.description {
  font-size: 1.2rem;
  color: #a0a0b3;
}

.contact-button {
  background-color: #4a90e2;
  color: #fff;
  border: none;
  padding: 12px 25px;
  font-size: 1.1rem;
  cursor: pointer;
  border-radius: 8px;
  margin-top: 20px;
}

.contact-button:hover {
  background-color: #3b7dc3;
}

.social-links a {
  margin: 0 15px;
  color: #add1fb;
  text-decoration: none;
  font-size: 1.2rem;
}

.social-links a:hover {
  color: #3b7dc3;
}

.social-logo {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  transition: opacity 0.3s ease;
}

/* Skills Section */
.skills {
  padding: 40px 20px;
  background-color: #333547;
  border-radius: 10px;
  margin-bottom: 30px;
  text-align: center;
}

.skills-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.skill-logo {
  position: relative;
  margin: 20px;
  transition: transform 0.3s ease;
}

.skill-logo img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  transition: opacity 0.3s ease;
}

.skill-logo:hover img {
  opacity: 0.3;
}

.skill-name {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  font-size: 1.2rem;
  display: none;
}

.skill-logo:hover .skill-name {
  display: block;
}

/* Experience Section */
.experience {
  padding: 40px 20px;
  background-color: #2c2f3b;
  border-radius: 10px;
  margin-bottom: 30px;
  text-align: center;
}

.experience h2 {
  font-size: 2rem;
  color: #fff;
  margin-bottom: 20px;
}

.experience ul {
  list-style-type: none;
}

.experience li {
  padding: 12px 20px;
  margin: 10px 0;
  background-color: #444a63;
  border-radius: 8px;
  transition: background-color 0.3s ease;
}

.experience li:hover {
  background-color: #6e7eaa;
}

.experience-logo {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  transition: opacity 0.3s ease;
}

.resume-link {
  margin: 0 15px;
  color: #add1fb;
  text-decoration: underline;
  font-size: 1.2rem;
}

footer {
  margin-top: 50px;
  font-size: 1rem;
  color: #a0a0b3;
}
